/* Mixins */
:root {
  --logo-size: 30px;
  --count-size: 30px;
}

@media (min-width: 768px) {
  :root {
    --logo-size: 36px;
    --count-size: 36px;
  }
}

* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

html {
  font-family: "Ubuntu", sans-serif;
  font-size: 100%;
  line-height: 1.5;
  color: #3d3d3d;
}

@media (min-width: 768px) {
  html {
    font-size: 112.5%;
  }
}

body {
  margin: 0;
  padding: 0;
  background: #f5f5f5;
}

a {
  color: #1f1f1f;
  outline: 0;
  transition: color 0.2s linear;
}

a:visited {
  color: #999;
}

a:hover {
  color: #ff6600;
}

a:active {
  color: #ff6600;
}

header {
  background: #ff6600;
  padding: 1rem;
}

@media (min-width: 1024px) {
  header {
    padding: 1.5rem;
  }
}

footer {
  padding: 1rem;
  background: #f5f5f5;
}

.container {
  padding: 0;
  background-color: #fff;
}

@media (min-width: 1024px) {
  .container {
    margin: 1.5rem 10% 0;
  }
}

@media (min-width: 1280px) {
  .container {
    margin: 1.5rem auto 0;
    max-width: 1180px;
  }
}

.primary-title {
  margin: 0;
}

.primary-title__link {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.primary-title__link:visited, .primary-title__link:hover {
  color: inherit;
}

.primary-title__text {
  flex: 1 0 auto;
  font-size: 1.2rem;
  color: #fff;
}

@media (min-width: 540px) {
  .primary-title__text--compact {
    display: none;
  }
}

.primary-title__text--full {
  display: none;
}

@media (min-width: 540px) {
  .primary-title__text--full {
    display: block;
  }
}

.primary-title__logo {
  flex: 0 1 var(--logo-size);
  width: var(--logo-size);
  height: var(--logo-size);
  margin: 0 1rem 0 0;
  border: 1px solid #fff;
}

@media (min-width: 1024px) {
  .primary-title__logo {
    margin: 0 1.5rem 0 0;
  }
}

.links-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.links-list__item {
  padding: 1rem 1rem 0;
}

@media (min-width: 1024px) {
  .links-list__item {
    padding: 1.5rem 1.5rem 0;
  }
}

.links-list__item:after {
  content: "";
  display: block;
  margin: 1rem 0 0 calc(var(--count-size) + 1rem);
  height: 1px;
  background-color: #d6d6d6;
  text-align: right;
}

@media (min-width: 1024px) {
  .links-list__item:after {
    margin: 1.5rem 0 0 calc(var(--count-size) + 1.5rem);
  }
}

.links-list__item:last-child {
  padding-bottom: 1rem;
}

@media (min-width: 1024px) {
  .links-list__item:last-child {
    padding-bottom: 1.5rem;
  }
}

.links-list__item:last-child:after {
  display: none;
}

.links-list__link {
  display: block;
  overflow: hidden;
  text-decoration: none;
}

@media (min-width: 1024px) {
  .links-list__link {
    font-size: 1.2rem;
  }
}

.links-list__link:visited .links-list__count {
  color: #999;
  border-color: #b7b7b7;
}

.links-list__link:hover .links-list__count, .links-list__link:active .links-list__count, .links-list__link:focus .links-list__count {
  color: #5b5b5b;
  border-color: #5b5b5b;
}

.links-list__content {
  display: flex;
  overflow: hidden;
  margin: 0 0 0 calc(var(--count-size) + 1rem);
  flex-direction: column;
}

@media (min-width: 1024px) {
  .links-list__content {
    margin: 0 0 0 calc(var(--count-size) + 1.5rem);
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.links-list__title {
  font-size: 1.2rem;
  line-height: 1.4;
  margin-bottom: 0.25rem;
}

@media (min-width: 1024px) {
  .links-list__title {
    margin-right: 0.5rem;
    margin-bottom: 0;
    line-height: 1.5;
  }
}

.links-list__source {
  color: #b7b7b7;
  font-weight: 400;
  font-style: italic;
}

.links-list__count {
  float: left;
  height: var(--count-size);
  width: var(--count-size);
  background-color: #fff;
  border: 1px solid #b7b7b7;
  color: #5b5b5b;
  font-size: 0.875rem;
  line-height: calc(var(--count-size) - 2px);
  text-align: center;
  transition: border 0.2s linear;
}

.links-list__meta {
  display: flex;
  overflow: hidden;
  margin-left: calc(var(--count-size) + 1rem);
  flex-direction: column;
}

@media (min-width: 540px) {
  .links-list__meta {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

@media (min-width: 1024px) {
  .links-list__meta {
    margin-left: calc(var(--count-size) + 1.5rem);
  }
}

.links-list__posted {
  color: #7a7a7a;
}

.links-list__comments {
  text-decoration: none;
}

@media (min-width: 540px) {
  .links-list__comments {
    margin-left: 1rem;
  }
}

.links-list__comments--no-comments {
  color: #999;
}

.article-title {
  position: relative;
  margin: 0 0 1rem 0;
  padding: 0 0 1rem 0;
  font-size: 1.2rem;
  line-height: 1.2;
}

.article-title:after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: var(--logo-size);
  height: 4px;
  background-color: #ff6600;
}

.article-title__link {
  text-decoration: none;
}

.article-title__source {
  display: block;
  color: #b7b7b7;
  font-weight: 400;
  font-style: italic;
}

@media (min-width: 1024px) {
  .article-title__source {
    display: inline;
    padding-left: 0.5rem;
  }
}

.comments {
  padding: 1rem;
}

@media (min-width: 1024px) {
  .comments {
    padding: 1.5rem;
  }
}

.comment {
  margin-bottom: 1rem;
}

.comment p {
  max-width: 620px;
  margin: 0;
  word-wrap: break-word;
}

.comment p:empty {
  display: none;
}

.comment p + p {
  margin: 1rem 0 0 0;
}

.comment a {
  overflow-wrap: break-word;
  word-wrap: break-word;
}

.comment pre {
  margin: 1rem 0;
  padding: 0.5rem;
  overflow: auto;
  background: #f5f5f5;
}

.comment code {
  padding: 0;
  margin: 0;
  font-family: "Ubuntu Mono", monospace;
}

.comment[data-level="0"] .comment__user {
  color: #ff6600;
  border-left-color: #ff6600;
}

.comment__meta {
  margin-bottom: 0.5rem;
}

.comment__user {
  padding: 0 0 0 0.5rem;
  margin-right: 0.5rem;
  color: #1f1f1f;
  font-weight: 700;
  border-left: 2px solid #d6d6d6;
}

.comment__time-ago {
  font-style: italic;
  color: #7a7a7a;
}

[data-level="1"] {
  padding-left: 1rem;
}

[data-level="2"] {
  padding-left: 2rem;
}

[data-level="3"] {
  padding-left: 3rem;
}

[data-level="4"] {
  padding-left: 4rem;
}

[data-level="5"] {
  padding-left: 5rem;
}

[data-level="6"] {
  padding-left: 6rem;
}

[data-level="7"] {
  padding-left: 7rem;
}

[data-level="8"] {
  padding-left: 8rem;
}

[data-level="9"] {
  padding-left: 9rem;
}

[data-level="10"] {
  padding-left: 10rem;
}

[data-level="11"] {
  padding-left: 11rem;
}

[data-level="12"] {
  padding-left: 12rem;
}

[data-level="13"] {
  padding-left: 13rem;
}

[data-level="14"] {
  padding-left: 14rem;
}

[data-level="15"] {
  padding-left: 15rem;
}

.attributions {
  text-align: center;
}

.attributions p {
  margin: 0;
}

.attributions a,
.attributions a:visited {
  color: #7a7a7a;
}

.attributions a:hover {
  color: #5b5b5b;
}

.no-news,
.no-comments {
  padding: 1rem;
}

.no-news > h3,
.no-comments > h3 {
  margin: 0 0 0.5rem 0;
}

.no-news > p,
.no-comments > p {
  margin: 0;
}
